.container {
  padding: 0 20px;
  width: 100%;
  z-index: 2; }
  @media (min-width: 1026px) {
    .container {
      padding: 0 60px;
      top: 0;
      left: 0; } }
  .container.fixed {
    position: fixed; }
  .container.gradient {
    background: linear-gradient(#000 30%, rgba(0, 0, 0, 0) 100%); }
  .container.dark-mode a {
    color: #fff !important; }

.inner {
  position: relative;
  width: 100%;
  height: 100%;
  padding-top: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.banner {
  width: 300px;
  height: 54px;
  background-size: 100%; }
  @media (min-width: 600px) {
    .banner {
      width: 500px;
      height: 88px; } }

.nav {
  margin-top: 20px; }
  @media (min-width: 600px) {
    .nav {
      margin-top: 20px; } }
  .nav li {
    font-size: 12px;
    font-weight: bold;
    display: inline-block; }
    @media (min-width: 600px) {
      .nav li {
        font-size: 18px; } }
    .nav li:not(:last-child) {
      margin-right: 15px; }
      @media (min-width: 600px) {
        .nav li:not(:last-child) {
          margin-right: 35px; } }
    .nav li:hover a, .nav li.active a {
      color: #FF1815; }
    .nav li:hover .underline, .nav li.active .underline {
      width: 100%; }
    .nav li .underline {
      width: 0;
      height: 2px;
      background: #FF1815;
      margin: 0 auto;
      transition: all 0.25s cubic-bezier(0.42, 0, 0.58, 1); }

.menu-container {
  margin-top: 30px;
  display: none; }
  @media (min-width: 600px) {
    .menu-container {
      display: none; } }
  .menu-container .button {
    width: 34px;
    height: 34px;
    display: flex;
    justify-content: center;
    background: #000000; }
  .menu-container ul.menu {
    visibility: hidden;
    opacity: 0;
    margin-top: 25px;
    padding: 12px 10px;
    position: absolute;
    background: #000000;
    border: 1px solid #FF1815;
    left: 50%;
    transform: translateX(-50%);
    transition: all 0.25s cubic-bezier(0.42, 0, 0.58, 1); }
    .menu-container ul.menu.active {
      visibility: visible;
      opacity: 1; }
  .menu-container li {
    font-size: 16px;
    color: #fff; }
    .menu-container li:not(:last-child) {
      margin-bottom: 10px; }
