.container {
  width: 100%; }

.paragraph {
  font-size: 18px;
  line-height: 34px;
  margin-bottom: 20px; }

.subheader {
  font-size: 20px;
  margin-bottom: 10px; }

.list {
  list-style-type: disc;
  margin-left: 20px;
  margin-bottom: 20px; }
  .list.ordered {
    list-style-type: decimal; }
  .list li {
    font-weight: bold; }
    .list li:not(:last-child) {
      padding-bottom: 10px; }

@media (min-width: 600px) {
  .paragraph,
  .subheader,
  .list {
    padding: 0 80px; } }

.image-container {
  margin-bottom: 20px; }
  .image-container.small {
    width: 100%; }
    @media (min-width: 600px) {
      .image-container.small {
        width: 300px; } }
  @media (min-width: 600px) {
    .image-container.left {
      float: left;
      margin: 0 20px 20px 0; } }
  @media (min-width: 600px) {
    .image-container.right {
      float: right;
      margin: 0 0 20px 20px; } }
