.container {
  width: 100%; }

.title {
  font-size: 30px;
  text-align: center;
  margin-bottom: 10px; }
  @media (min-width: 600px) {
    .title {
      font-size: 48px; } }

.byline {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  margin-bottom: 15px; }
  .byline .image {
    width: 40px;
    height: 40px;
    background-size: cover;
    display: inline-block;
    border-radius: 50%;
    margin-right: 10px;
    transition: 0.25s all cubic-bezier(0.42, 0, 0.58, 1); }
  .byline a .image:hover {
    filter: brightness(75%); }

.primary-asset {
  width: 100%;
  margin-bottom: 15px; }
