@keyframes fadein {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.size-container {
  margin: 0 auto;
  height: inherit; }

.image-container {
  position: relative;
  height: inherit;
  z-index: 0; }
  .image-container.placeholder:after {
    content: '';
    display: block;
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-size: auto 35%;
    background-position: center center;
    opacity: .1;
    background-color: #666;
    background-image: 'url(./image-placeholder.png)'; }

.image-container:not(.loaded) .image {
  display: none; }

.image {
  position: relative;
  z-index: 1;
  display: block;
  width: 100%;
  height: 100%;
  animation: fadein 0.25s cubic-bezier(0.25, 0.1, 0.25, 1) both; }
  .image.fit {
    object-fit: contain; }
  .image.cover {
    object-fit: cover; }

.lock-ratio .image {
  position: absolute;
  height: 100%; }
