.container {
  width: 100%;
  background: #232323;
  background-size: cover;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center; }
  @media (min-width: 600px) {
    .container {
      justify-content: center;
      background-attachment: fixed; } }
  .container.justify-top {
    justify-content: flex-start; }
    .container.justify-top .text-container {
      background: linear-gradient(rgba(0, 0, 0, 0.7) 50%, rgba(0, 0, 0, 0) 0%);
      padding-top: 20px; }
      @media (min-width: 600px) {
        .container.justify-top .text-container {
          padding-top: 40px; } }
  .container.justify-bottom {
    justify-content: flex-end; }
    .container.justify-bottom .text-container {
      background: linear-gradient(rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 50%);
      padding-bottom: 20px; }
      @media (min-width: 600px) {
        .container.justify-bottom .text-container {
          padding-bottom: 30px; } }
  .container.full {
    min-height: 100vh; }
  .container.three-quater {
    min-height: 35vh; }
    @media (min-width: 600px) {
      .container.three-quater {
        min-height: 75vh; } }
  .container.half {
    min-height: 35vh; }
    @media (min-width: 600px) {
      .container.half {
        min-height: 50vh; } }
  .container.auto {
    min-height: auto; }

.text-container {
  width: 100%;
  text-align: center; }
  .text-container.border {
    border: 1px solid #fff;
    padding: 18px 20px; }

.title {
  font-size: 35px;
  line-height: 1.3;
  color: #fff;
  text-align: center; }
  @media (min-width: 600px) {
    .title {
      font-size: 60px; } }

.buttons-container {
  margin-top: 40px; }
  @media (min-width: 600px) {
    .buttons-container {
      margin-top: 60px; } }

.button-container {
  text-align: center; }
  .button-container:not(:last-child) {
    margin: 0 0 40px 0; }
  @media (min-width: 600px) {
    .button-container.row {
      display: inline-block; }
      .button-container.row:not(:last-child) {
        margin: 0 30px 0 0; } }
  .button-container a {
    color: #fff; }

.label {
  font-size: 16px;
  color: #fff; }

.button {
  font-size: 18px;
  color: #fff;
  cursor: pointer;
  transition: all 0.25s cubic-bezier(0.42, 0, 0.58, 1); }
  @media (min-width: 600px) {
    .button {
      font-size: 20px; } }
  .button.border {
    border: 1px solid #fff;
    padding: 16px 30px; }
    .button.border:hover {
      background: #fff; }
      .button.border:hover a {
        color: #005CAF; }

.image-container {
  width: 100%;
  margin-top: 40px;
  box-shadow: 0 0 10px 10px #000000;
  cursor: pointer;
  transition: all 0.25 cubic-bezier(0.42, 0, 0.58, 1); }
  @media (min-width: 600px) {
    .image-container {
      width: 500px;
      margin-top: 60px; } }
  .image-container:hover {
    box-shadow: 0 0 10px 15px #000000; }
  .image-container img {
    width: 100%; }
